export class FrameMng implements T_GetFrm {
	static	#cfg	: Config;
	static	#sys	: SysBase;
	static	#main	: T_Main;
	static	init(cfg: Config, sys: SysBase, main: T_Main): void {
		FrameMng.#cfg = cfg;
		FrameMng.#sys = sys;
		FrameMng.#main = main;
	}

	constructor(hTag: T_HTag, private readonly appPixi: Application, private readonly val: T_Variable) {
		//	HTMLãƒ•ãƒ¬ãƒ¼ãƒ 
		hTag.add_frame		= o=> this.#add_frame(o);	// ãƒ•ãƒ¬ãƒ¼ãƒ è¿½åŠ 
		hTag.let_frame		= o=> this.#let_frame(o);	// ãƒ•ãƒ¬ãƒ¼ãƒ å¤‰æ•°ã‚’å–å¾—
		hTag.set_frame		= o=> this.#set_frame(o);	// ãƒ•ãƒ¬ãƒ¼ãƒ å¤‰æ•°ã«è¨­å®š
		hTag.frame			= o=> this.#frame(o);		// ãƒ•ãƒ¬ãƒ¼ãƒ ã«è¨­å®š
		hTag.tsy_frame		= o=> this.#tsy_frame(o);	// ãƒ•ãƒ¬ãƒ¼ãƒ ã‚’ãƒˆã‚¥ã‚¤ãƒ¼ãƒ³é–‹å§‹
	}

	#evtMng	: IEvtMng;
	setEvtMng(evtMng: IEvtMng) {this.#evtMng = evtMng}

	// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
	#hIfrm	: {[id: string]: HTMLIFrameElement} = Object.create(null);
	destroy() {
		// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
		for (const f of Object.values(this.#hIfrm)) f.parentElement!.removeChild(f);
		// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
		this.#hIfrm = Object.create(null);
	}

	hideAllFrame() {	// ï¼ˆè¡¨ç¤ºãƒ»éè¡¨ç¤ºã‚’ä¿å­˜ã—ã¤ã¤ï¼‰ã™ã¹ã¦éè¡¨ç¤ºã«
		for (const [id, {style}] of Object.entries(this.#hIfrm)) {
			this.#hIfrmVisibleBk[id] = style.display !== 'none';
			style.display = 'none';
		}
	}
	// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
	#hIfrmVisibleBk: {[id: string]: boolean} = Object.create(null);
	restoreAllFrame() {	// ä¿å­˜ã—ã¦ã„ãŸè¡¨ç¤ºãƒ»éè¡¨ç¤ºã‚’å›å¾©
		for (const [id, v] of Object.entries(this.#hIfrmVisibleBk)) {
			const f = this.#hIfrm[id];
			if (f) f.style.display = v ?'inline' :'none';
		}
		// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
		this.#hIfrmVisibleBk = Object.create(null);
	}

	//	HTMLãƒ•ãƒ¬ãƒ¼ãƒ 
	// ãƒ•ãƒ¬ãƒ¼ãƒ è¿½åŠ 
	#add_frame(hArg: TArg) {
		const {id, src, alpha: a=1, scale_x: sx=1, scale_y: sy=1, rotate: r=0,} = hArg;
		if (! id) throw 'idã¯å¿…é ˆã§ã™';
		if (! src) throw 'srcã¯å¿…é ˆã§ã™';
		const vn = 'const.sn.frm.'+ id;
		if (this.val.getVal(`tmp:${vn}`)) throw `frameã€${id}ã€‘ã¯ã™ã§ã«ã‚ã‚Šã¾ã™`;

		const v = argChk_Boolean(hArg, 'visible', true);
		const b_color = hArg.b_color ?` background-color: ${hArg.b_color};` :'';
		const rct = this.#rect(hArg);
		// ã€sandbox="allow-scripts allow-same-origin"ã€‘ã¯å¿…è¦ãªã®ã«è­¦å‘ŠãŒå‡ºã‚‹ã®ã§å‰Šé™¤
		FrameMng.#main.cvs.insertAdjacentHTML('beforebegin', `<iframe id="${id}" style="opacity: ${String(a)}; ${b_color} position: absolute; left:${
			String(FrameMng.#sys.ofsLeft4elm +rct.x *FrameMng.#sys.cvsScale)
		}px; top: ${
			String(FrameMng.#sys.ofsTop4elm +rct.y *FrameMng.#sys.cvsScale)
		}px; z-index: 1; border: 0px; overflow: hidden; display: ${
			v ?'inline' :'none'
		}; transform: scale(${String(sx)}, ${String(sy)}) rotate(${
			String(r)}deg);" width="${
			String(rct.width *FrameMng.#sys.cvsScale)
		}" height="${
			String(rct.height *FrameMng.#sys.cvsScale)
		}"></iframe>`);

		const RPN_ADD_FRAME = Reading.procID +`add_frame id:${id}`;
		Reading.beginProc(RPN_ADD_FRAME);
		const url = FrameMng.#cfg.searchPath(src, SEARCH_PATH_ARG_EXT.HTML);
		const ld = (new Loader)
		.add({name: src, url, xhrType: LoaderResource.XHR_RESPONSE_TYPE.TEXT});
		if (FrameMng.#sys.arg.crypto) ld.use(
			// eslint-disable-next-line @typescript-eslint/no-unsafe-argument
			(res, next)=> void FrameMng.#sys.dec(res.extension, res.data)
			.then(r=> {
				res.data = r;
				next();
			})
			.catch((e: unknown)=> {
				FrameMng.#main.errScript(`[add_frame]Html ãƒ­ãƒ¼ãƒ‰å¤±æ•—ã§ã™ src:${res.name} ${String(e)}`, false);
				next();
			})
		);
		ld.load((_ldr, hRes)=> {
			const f = <HTMLIFrameElement>document.getElementById(id);
			this.#hIfrm[id] = f;
			this.#hDisabled[id] = false;

			const iLastSep = url.lastIndexOf('/') +1;
			const path_parent = url.slice(0, iLastSep);
			const path_pa_pa = path_parent.slice(0, iLastSep);
			f.srcdoc = String(hRes[src]?.data)	// .src ã¯ãµã‚Šãƒ¼ã‚€ã§å•é¡Œç™ºç”Ÿ
			.replace('sn_repRes();', '')	// ã“ã‚Œã¯ã„ãšã‚Œã‚„ã‚ã‚‹
			.replaceAll(
				/\s(?:src|href)=(["'])(\S+?)\1/g,	// ã€\sã€‘ãŒå¤§äº‹ã€data-srcå¼¾ã
				(m, br: string, v: string)=> v.startsWith('../')
				? path_pa_pa + m.slice(3)
				: m.replace('./', '')	// ã€Œ./ã€ã¯ç„¡è¦–
					.replace(br, br + path_parent)
			);

			if (f.srcdoc.includes('true/*WEBP*/;')) f.srcdoc = f.srcdoc.replaceAll(
				/data-src="(.+?\.)(?:jpe?g|png)/g,
				(_, p1: string)=> `data-src="${p1}webp`
			);

			f.onload = ()=> {	// ä¸€åº¦å¤‰æ•°ã«å…¥ã‚Œã¦ã“ã“ã§è¨­å®šã™ã‚‹ã®ã¯Firefoxå¯¾å¿œã€‚ifrm.onloadãŒäºŒåº¦å‘¼ã°ã‚Œã‚‹ï¼
				Reading.endProc(RPN_ADD_FRAME);

				// çµ„ã¿è¾¼ã¿å¤‰æ•°
				this.val.setVal_Nochk('tmp', vn, true);
				this.val.setVal_Nochk('tmp', vn +'.alpha', a);
				this.val.setVal_Nochk('tmp', vn +'.x', rct.x);
				this.val.setVal_Nochk('tmp', vn +'.y', rct.y);
				this.val.setVal_Nochk('tmp', vn +'.scale_x', sx);
				this.val.setVal_Nochk('tmp', vn +'.scale_y', sy);
				this.val.setVal_Nochk('tmp', vn +'.rotate', r);
				this.val.setVal_Nochk('tmp', vn +'.width', rct.width);
				this.val.setVal_Nochk('tmp', vn +'.height', rct.height);
				this.val.setVal_Nochk('tmp', vn +'.visible', v);

				// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
				const win = f.contentWindow!;
				this.#evtMng.resvFlameEvent(<HTMLBodyElement>win.document.body);
				// sn_repRes()ã‚’ã‚³ãƒ¼ãƒ«ã€‚å¼•æ•°ã¯ç”»åƒãƒ­ãƒ¼ãƒ‰å‡¦ç†å·®ã—æ›¿ãˆãƒ¡ã‚½ãƒƒãƒ‰
				// eslint-disable-next-line @typescript-eslint/no-unsafe-call, @typescript-eslint/no-unsafe-member-access, @typescript-eslint/no-explicit-any
				(<any>win).sn_repRes?.((i: HTMLImageElement)=> FrameMng.#loadPic2Img(i.dataset.src ?? '', i));
			};
		});
		return true;
	}
	#hDisabled	: {[id: string]: boolean}	= {};
	// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
	getFrmDisabled(id: string): boolean {return this.#hDisabled[id]!}
	#rect(hArg: TArg): DOMRect {
		const a = {...hArg};
		return new DOMRect(
			argChk_Num(a, 'x', 0),
			argChk_Num(a, 'y', 0),
			argChk_Num(a, 'width', CmnLib.stageW),
			argChk_Num(a, 'height', CmnLib.stageH),
		);
	}

	static	#loadPic2Img(src: string, img: HTMLImageElement, onload?: (img2: HTMLImageElement)=> void) {
		const oUrl = this.#hEncImgOUrl[src];
		if (oUrl) {
			img.src = oUrl;
			if (onload) img.onload = ()=> onload(img);
			return;
		}

		const aImg = this.#hARetImg[src];
		if (aImg) {aImg.push(img); return}	// load çµ‚äº†å‰ã®é§†ã‘è¾¼ã¿å¯¾å¿œ
		this.#hARetImg[src] = [img];

		const [srcNoPrm='', Prm=''] = src.split('?');
		const path = FrameMng.#cfg.searchPath(srcNoPrm, SEARCH_PATH_ARG_EXT.SP_GSM);
		const ld2 = (new Loader)
		.add({name: src, url: path, xhrType: LoaderResource.XHR_RESPONSE_TYPE.BUFFER,});

		// === vite-electron ç”¨ã‚³ãƒ¼ãƒ‰ ===
		if (FrameMng.#sys.use4ViteElectron(src, path, ld2, FrameMng.#main)) { /* empty */ }
		else
		if (FrameMng.#sys.arg.crypto && path.endsWith('.bin')) ld2.use((res, next)=> {
			if (res.extension !== 'bin') {next(); return}

			// eslint-disable-next-line @typescript-eslint/no-unsafe-argument
			void FrameMng.#sys.decAB(res.data)
			.then(r=> {
				res.data = r;
				if (r instanceof HTMLImageElement) {
					res.type = LoaderResource.TYPE.IMAGE;

					// ã“ã‚Œã‚’ã™ã‚‹ã¨äºŒåº¦ç›®ã®ãƒ•ãƒ¬ãƒ¼ãƒ ç”»é¢è¡¨ç¤ºã§ç”»åƒãŒå£Šã‚Œã‚‹
					// next();
					// URL.revokeObjectURL(r.src);
					// return;
				}
				next();
			})
			.catch((e: unknown)=> {
				FrameMng.#main.errScript(`FrameMng loadPic ãƒ­ãƒ¼ãƒ‰å¤±æ•—ã§ã™ fn:${res.name} ${String(e)}`, false)
				next();
			});
		});
		ld2.load((_ldr, hRes)=> {
			for (const [s2, {data: {src}}] of Object.entries(hRes)) {
				// eslint-disable-next-line @typescript-eslint/restrict-plus-operands
				const u2 = this.#hEncImgOUrl[s2] = src + (
					// eslint-disable-next-line @typescript-eslint/no-unsafe-call, @typescript-eslint/no-unsafe-member-access
					src.startsWith('blob:') || src.startsWith('data:')
					?'' :Prm ? '?'+ Prm: ''
				);
				const ri = this.#hARetImg[s2];
				if (ri) for (const i of ri) {
					i.src = u2;
					if (onload) i.onload = ()=> onload(i);
				}
				// eslint-disable-next-line @typescript-eslint/no-dynamic-delete
				delete this.#hARetImg[s2];
			//	URL.revokeObjectURL(u2);// ç”»é¢é·ç§»ã§æ¯å›å†ç”Ÿæˆã™ã‚‹ã®ã§
			}
		});
	}
	static	#hARetImg		: {[src: string]: HTMLImageElement[]}	= {};
	static	#hEncImgOUrl	: {[src: string]: string}				= {};


	cvsResize() {	// NOTE: ãƒ•ã‚©ãƒ³ãƒˆã‚µã‚¤ã‚ºã¯ã©ã†å¤‰æ›´ã™ã¹ãã‹
		for (const [id, f] of Object.entries(this.#hIfrm)) {
			const vn = 'const.sn.frm.'+ id;
			const x = Number(this.val.getVal(vn +'.x'));
			const y = Number(this.val.getVal(vn +'.y'));
			const w = Number(this.val.getVal(vn +'.width'));
			const h = Number(this.val.getVal(vn +'.height'));
			f.style.left = `${String(
				FrameMng.#sys.ofsLeft4elm +x *FrameMng.#sys.cvsScale
			)}px`;
			f.style.top  = `${String(
				FrameMng.#sys.ofsTop4elm  +y *FrameMng.#sys.cvsScale
			)}px`;
			f.width = String(w *FrameMng.#sys.cvsScale);
			f.height = String(h *FrameMng.#sys.cvsScale);
		}
	}

	// ãƒ•ãƒ¬ãƒ¼ãƒ å¤‰æ•°ã‚’å–å¾—
	#let_frame(hArg: TArg) {
		const {id, var_name} = hArg;
		if (! id) throw 'idã¯å¿…é ˆã§ã™';
		const f = <HTMLIFrameElement | null>document.getElementById(id);
		if (! f) throw `idã€${id}ã€‘ã¯ãƒ•ãƒ¬ãƒ¼ãƒ ã§ã¯ã‚ã‚Šã¾ã›ã‚“`;
		const vn = 'const.sn.frm.'+ id;
		if (! this.val.getVal(`tmp:${vn}`)) throw `frameã€${id}ã€‘ãŒèª­ã¿è¾¼ã¾ã‚Œã¦ã„ã¾ã›ã‚“`;
		if (! var_name) throw 'var_nameã¯å¿…é ˆã§ã™';

		// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
		const win = f.contentWindow!;
		if (! Object.hasOwn(win, var_name)) throw `frameã€${id}ã€‘ã«å¤‰æ•°/é–¢æ•°ã€${var_name}ã€‘ãŒã‚ã‚Šã¾ã›ã‚“ã€‚å¤‰æ•°ã¯ varä»˜ãã«ã—ã¦ä¸‹ã•ã„`;

		// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
		const v = win[<keyof Window>var_name];
		// varå¤‰æ•° / é–¢æ•°å®Ÿè¡Œã®æˆ»ã‚Šå€¤ -> çµ„ã¿è¾¼ã¿å¤‰æ•°
		this.val.setVal_Nochk(
			'tmp',
			vn +'.'+ var_name,
			// eslint-disable-next-line @typescript-eslint/no-unsafe-argument, @typescript-eslint/no-unsafe-call
			argChk_Boolean(hArg, 'function', false) ?v() :v
		);

		return false;
	}

	// ãƒ•ãƒ¬ãƒ¼ãƒ å¤‰æ•°ã«è¨­å®š
	#set_frame(hArg: TArg) {
		const {id, var_name, text} = hArg;
		if (! id) throw 'idã¯å¿…é ˆã§ã™';
		const f = <HTMLIFrameElement | null>document.getElementById(id);
		if (! f) throw `idã€${id}ã€‘ã¯ãƒ•ãƒ¬ãƒ¼ãƒ ã§ã¯ã‚ã‚Šã¾ã›ã‚“`;
		const vn = 'const.sn.frm.'+ id;
		if (! this.val.getVal(`tmp:${vn}`)) throw `frameã€${id}ã€‘ãŒèª­ã¿è¾¼ã¾ã‚Œã¦ã„ã¾ã›ã‚“`;
		if (! var_name) throw 'var_nameã¯å¿…é ˆã§ã™';
		if (! text) throw 'textã¯å¿…é ˆã§ã™';

		// -> çµ„ã¿è¾¼ã¿å¤‰æ•°
		this.val.setVal_Nochk('tmp', vn +'.'+ var_name, text);

		// -> varå¤‰æ•°ã«è¨­å®š
		// eslint-disable-next-line @typescript-eslint/no-explicit-any, @typescript-eslint/no-non-null-assertion
		const win: any = f.contentWindow!;
		// eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
		win[var_name] = text;

		return false;
	}

	// ãƒ•ãƒ¬ãƒ¼ãƒ ã«è¨­å®š
	#zIdx = 1;
	#frame(hArg: TArg) {
		const {id} = hArg;
		if (! id) throw 'idã¯å¿…é ˆã§ã™';
		const f = <HTMLIFrameElement | null>document.getElementById(id);
		if (! f) throw `idã€${id}ã€‘ã¯ãƒ•ãƒ¬ãƒ¼ãƒ ã§ã¯ã‚ã‚Šã¾ã›ã‚“`;
		const vn = 'const.sn.frm.'+ id;
		if (! this.val.getVal('tmp:'+ vn)) throw `frameã€${id}ã€‘ãŒèª­ã¿è¾¼ã¾ã‚Œã¦ã„ã¾ã›ã‚“`;

		const s = f.style;
		if (argChk_Boolean(hArg, 'float', false)) s.zIndex = String(++this.#zIdx);
		else if ('index' in hArg) s.zIndex = String(argChk_Num(hArg, 'index', 0));
		else if (hArg.dive) s.zIndex = String(-++this.#zIdx);	// -ã¯å¿…è¦

		if ('alpha' in hArg) {
			const a = s.opacity = String(hArg.alpha);
			this.val.setVal_Nochk('tmp', vn +'.alpha', a);
		}
		const rct = this.#rect(hArg);
		if ('x' in hArg || 'y' in hArg) {
			s.left = `${String(
				FrameMng.#sys.ofsLeft4elm +rct.x *FrameMng.#sys.cvsScale
			)}px`;
			s.top  = `${String(
				FrameMng.#sys.ofsTop4elm  +rct.y *FrameMng.#sys.cvsScale
			)}px`;
			this.val.setVal_Nochk('tmp', vn +'.x', rct.x);
			this.val.setVal_Nochk('tmp', vn +'.y', rct.y);
		}
		if ('scale_x' in hArg || 'scale_y' in hArg || 'rotate' in hArg) {
			const sx = argChk_Num(hArg, 'scale_x', 1);
			const sy = argChk_Num(hArg, 'scale_y', 1);
			const r = argChk_Num(hArg, 'rotate', 0);
			s.transform = `scale(${String(sx)}, ${String(sy)}) rotate(${String(r)}deg)`;
			this.val.setVal_Nochk('tmp', vn +'.scale_x', sx);
			this.val.setVal_Nochk('tmp', vn +'.scale_y', sy);
			this.val.setVal_Nochk('tmp', vn +'.rotate', r);
		}
		if ('width' in hArg) {
			f.width = String(rct.width *FrameMng.#sys.cvsScale);
			this.val.setVal_Nochk('tmp', vn +'.width', rct.width);
		}
		if ('height' in hArg) {
			f.height = String(rct.height *FrameMng.#sys.cvsScale);
			this.val.setVal_Nochk('tmp', vn +'.height', rct.height);
		}
		if ('visible' in hArg) {
			const v = argChk_Boolean(hArg, 'visible', true);
			s.display = v ?'inline' :'none';
			this.val.setVal_Nochk('tmp', vn +'.visible', v);
		}
		// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
		if ('b_color' in hArg) s.backgroundColor = hArg.b_color!;
		if ('disabled' in hArg) {
			const d = this.#hDisabled[id] = argChk_Boolean(hArg, 'disabled', true);
			// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
			const b = f.contentDocument!.body;
			for (const e of [
				...Array.from(b.getElementsByTagName('input')),
				...Array.from(b.getElementsByTagName('select')),
			]) e.disabled = d;
		}

		return false;
	}

	// ãƒ•ãƒ¬ãƒ¼ãƒ ã‚’ãƒˆã‚¥ã‚¤ãƒ¼ãƒ³é–‹å§‹
	#tsy_frame(hArg: TArg) {
		const {id, alpha, x, y, scale_x, scale_y, rotate, width, height} = hArg;
		if (! id) throw 'idã¯å¿…é ˆã§ã™';
		const f = <HTMLIFrameElement | null>document.getElementById(id);
		if (! f) throw `idã€${id}ã€‘ã¯ãƒ•ãƒ¬ãƒ¼ãƒ ã§ã¯ã‚ã‚Šã¾ã›ã‚“`;
		const vn = 'const.sn.frm.'+ id;
		if (! this.val.getVal(`tmp:${vn}`, 0)) throw `frameã€${id}ã€‘ãŒèª­ã¿è¾¼ã¾ã‚Œã¦ã„ã¾ã›ã‚“`;

		const hNow: {[val_name: string]: number} = {};
		if (alpha) hNow.a = Number(f.style.opacity);
		if (x || y || scale_x || scale_y || rotate) {
			hNow.x = Number(this.val.getVal(`tmp:${vn}.x`));
			hNow.y = Number(this.val.getVal(`tmp:${vn}.y`));
			hNow.sx = Number(this.val.getVal(`tmp:${vn}.scale_x`));
			hNow.sy = Number(this.val.getVal(`tmp:${vn}.scale_y`));
			hNow.r = Number(this.val.getVal(`tmp:${vn}.rotate`));
		}
		if (width) hNow.w = Number(this.val.getVal(`tmp:${vn}.width`));
		if (height) hNow.h = Number(this.val.getVal(`tmp:${vn}.height`));
		const hArg2 = CmnTween.cnvTweenArg(hArg, hNow);

		const hTo: {[val_name: string]: number} = {};
		let fncA = (_d: {a: number})=> { /* empty */ };
		if (alpha) {
			hTo.a = argChk_Num(hArg2, 'alpha', 0);
			fncA = d=> {
				f.style.opacity = String(d.a);
				this.val.setVal_Nochk('tmp', 'alpha', d.a);
			};
		}
		let fncXYSR = (_d: {
			x: number;
			y: number;
			sx: number;
			sy: number;
			r: number;
		})=> { /* empty */ };
		const rct = this.#rect(hArg2);
		if (x || y || scale_x || scale_y || rotate) {
			hTo.x = rct.x;
			hTo.y = rct.y;
			hTo.sx = argChk_Num(hArg2, 'scale_x', 1);
			hTo.sy = argChk_Num(hArg2, 'scale_y', 1);
			hTo.r = argChk_Num(hArg2, 'rotate', 0);
			fncXYSR = d=> {
				f.style.left = `${String(
					FrameMng.#sys.ofsLeft4elm +d.x *FrameMng.#sys.cvsScale
				)} px`;
				f.style.top  = `${String(
					FrameMng.#sys.ofsTop4elm  +d.y *FrameMng.#sys.cvsScale
				)} px`;
				f.style.transform = `scale(${String(d.sx)}, ${String(d.sy)}) rotate(${String(d.r)}deg)`;
				this.val.setVal_Nochk('tmp', vn +'.x', d.x);
				this.val.setVal_Nochk('tmp', vn +'.y', d.y);
				this.val.setVal_Nochk('tmp', vn +'.scale_x', d.sx);
				this.val.setVal_Nochk('tmp', vn +'.scale_y', d.sy);
				this.val.setVal_Nochk('tmp', vn +'.rotate', d.r);
			};
		}
		let fncW = (_d: {w: number})=> { /* empty */ };
		if (width) {
			hTo.w = rct.width;
			fncW = d=> {
				f.width = `${String(d.w *FrameMng.#sys.cvsScale)} px`;
				this.val.setVal_Nochk('tmp', vn +'.width', d.w);
			};
		}
		let fncH = (_d: {h: number})=> { /* empty */ };
		if (height) {
			hTo.h = rct.height;
			fncH = d=> {
				f.height = `${String(d.h *FrameMng.#sys.cvsScale)} px`;
				this.val.setVal_Nochk('tmp', vn +'.height', d.h);
			};
		}

		this.appPixi.stage.interactive = false;
		CmnTween.tween(`frm\n${id}`, hArg, hNow, CmnTween.cnvTweenArg(hArg, hNow), d=> {
			// eslint-disable-next-line @typescript-eslint/no-unsafe-argument
			fncA(d); fncXYSR(d); fncW(d); fncH(d);
		}, ()=> {this.appPixi.stage.interactive = true}, ()=> { /* empty */ });

		return false;
	}

}
